.xdsoft_datetimepicker {
  box-shadow: 0 5px 15px -5px rgba(0, 0, 0, 0.506);
  background: #fff;
  // border-bottom: 1px solid #bbb
  // border-left: 1px solid #ccc
  // border-right: 1px solid #ccc
  // border-top: 1px solid #ccc
  color: #333;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  padding: 8px;
  padding-left: 0;
  padding-top: 2px;
  position: absolute;
  z-index: 9999;
  box-sizing: border-box;
  width: 240px;
  border-radius: 0 0 5px 5px;
  margin-top: 1px;
  // border-top: 1px solid #cbd3d6
  display: none;
  &.xdsoft_rtl {
    padding: 8px 0 8px 8px; }
  iframe {
    position: absolute;
    left: 0;
    top: 0;
    width: 75px;
    height: 210px;
    background: transparent;
    border: 0; }
  button {
    border: none !important; } }

.xdsoft_noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
  &::selection, &::-moz-selection {
    background: transparent; } }

.xdsoft_datetimepicker {
  &.xdsoft_inline {
    display: inline-block;
    position: static;
    box-shadow: none; }
  * {
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0;
    margin: 0; }
  .xdsoft_datepicker, .xdsoft_timepicker {
    display: none; }
  .xdsoft_datepicker.active, .xdsoft_timepicker.active {
    display: block; }
  .xdsoft_datepicker {
    width: 224px;
    float: left;
    margin-left: 8px; }
  &.xdsoft_rtl .xdsoft_datepicker {
    float: right;
    margin-right: 8px;
    margin-left: 0; }
  &.xdsoft_showweeks .xdsoft_datepicker {
    width: 256px; }
  .xdsoft_timepicker {
    width: 224px;
    float: left;
    text-align: center;
    margin-left: 8px;
    margin-top: 0; }
  &.xdsoft_rtl .xdsoft_timepicker {
    float: right;
    margin-right: 8px;
    margin-left: 0; }
  .xdsoft_datepicker.active + .xdsoft_timepicker {
    margin-top: 8px;
    margin-bottom: 3px; }
  .xdsoft_monthpicker {
    position: relative;
    text-align: center; }
  .xdsoft_label i, .xdsoft_prev, .xdsoft_next, .xdsoft_today_button {
    background-image: url(data:image/png;base64,); }
  .xdsoft_label i {
    opacity: .5;
    background-position: -92px -19px;
    display: inline-block;
    width: 9px;
    height: 20px;
    vertical-align: middle; }
  .xdsoft_prev {
    float: left;
    background-position: -20px 0; }
  .xdsoft_today_button {
    float: left;
    background-position: -70px 0;
    margin-left: 5px; }
  .xdsoft_next {
    float: right;
    background-position: 0 0;
    background-color: transparent;
    background-repeat: no-repeat;
    border: 0 none;
    cursor: pointer;
    display: block;
    height: 30px;
    opacity: .5;
    -ms-filter: "alpha(opacity=50)";
    outline: medium none;
    overflow: hidden;
    padding: 0;
    position: relative;
    text-indent: 100%;
    white-space: nowrap;
    width: 20px;
    min-width: 0; }
  .xdsoft_prev, .xdsoft_today_button {
    background-color: transparent;
    background-repeat: no-repeat;
    border: 0 none;
    cursor: pointer;
    display: block;
    height: 30px;
    opacity: .5;
    -ms-filter: "alpha(opacity=50)";
    outline: medium none;
    overflow: hidden;
    padding: 0;
    position: relative;
    text-indent: 100%;
    white-space: nowrap;
    width: 20px;
    min-width: 0; }
  .xdsoft_timepicker {
    .xdsoft_prev, .xdsoft_next {
      float: none;
      background-position: -40px -15px;
      height: 15px;
      width: 30px;
      display: block;
      margin-left: 44%;
      margin-top: 7px; } }
  &.xdsoft_rtl .xdsoft_timepicker {
    .xdsoft_prev, .xdsoft_next {
      float: none;
      margin-left: 0;
      margin-right: 14px; } }
  .xdsoft_timepicker {
    .xdsoft_prev {
      background-position: -40px 0;
      margin-bottom: 7px;
      margin-top: 0; }
    .xdsoft_time_box {
      width: 100%;
      height: 151px;
      overflow: hidden;
      border-bottom: 1px solid #ddd;
      > div > div {
        background: #f5f5f5;
        border-top: 1px solid #ddd;
        color: #666;
        font-size: 12px;
        text-align: center;
        border-collapse: collapse;
        cursor: pointer;
        border-bottom-width: 0;
        height: 25px;
        line-height: 25px;
        &:first-child {
          border-top-width: 0; } } } }
  .xdsoft_today_button:hover, .xdsoft_next:hover, .xdsoft_prev:hover {
    opacity: 1;
    -ms-filter: "alpha(opacity=100)"; }
  .xdsoft_label {
    display: inline;
    position: relative;
    z-index: 9999;
    margin: 0;
    padding: 5px 3px;
    font-size: 14px;
    line-height: 20px;
    font-weight: bold;
    background-color: #fff;
    float: left;
    width: 182px;
    text-align: center;
    cursor: pointer;
    &:hover {
      > span {
        text-decoration: underline; }
      i {
        opacity: 1.0; } }
    > .xdsoft_select {
      border: 1px solid #ccc;
      position: absolute;
      right: 0;
      top: 30px;
      z-index: 101;
      display: none;
      background: #fff;
      max-height: 160px;
      overflow-y: hidden;
      &.xdsoft_monthselect {
        right: -7px; }
      &.xdsoft_yearselect {
        right: 2px; }
      > div > .xdsoft_option {
        &:hover {
          color: #fff;
          background: #ff8000; }
        padding: 2px 10px 2px 5px;
        text-decoration: none !important;
        &.xdsoft_current {
          background: #3af;
          box-shadow: #178fe5 0 1px 3px 0 inset;
          color: #fff;
          font-weight: 700; } } } }
  .xdsoft_month {
    width: 100px;
    text-align: right; }
  .xdsoft_calendar {
    clear: both; }
  .xdsoft_year {
    width: 48px;
    margin-left: 5px; }
  .xdsoft_calendar {
    table {
      border-collapse: collapse;
      width: 100%; }
    td > div {
      padding-right: 5px; }
    th {
      height: 25px; }
    td, th {
      width: 14.2857142%;
      background: #f5f5f5;
      border: 1px solid #ddd;
      color: #666;
      font-size: 12px;
      text-align: right;
      vertical-align: middle;
      padding: 0;
      border-collapse: collapse;
      cursor: pointer;
      height: 25px; } }
  &.xdsoft_showweeks .xdsoft_calendar {
    td, th {
      width: 12.5%; } }
  .xdsoft_calendar {
    th {
      background: #f1f1f1; }
    td {
      &.xdsoft_today {
        color: #23a8d1; }
      &.xdsoft_highlighted_default {
        background: #ffe9d2;
        box-shadow: #ffb871 0 1px 4px 0 inset;
        color: #000; }
      &.xdsoft_highlighted_mint {
        background: #c1ffc9;
        box-shadow: #00dd1c 0 1px 4px 0 inset;
        color: #000; }
      &.xdsoft_default, &.xdsoft_current {
        background: #3af;
        box-shadow: #178fe5 0 1px 3px 0 inset;
        color: #fff;
        font-weight: 700; } } }
  .xdsoft_timepicker .xdsoft_time_box > div > div.xdsoft_current {
    background: #3af;
    box-shadow: #178fe5 0 1px 3px 0 inset;
    color: #fff;
    font-weight: 700; }
  .xdsoft_calendar td {
    &.xdsoft_other_month, &.xdsoft_disabled {
      opacity: .5;
      -ms-filter: "alpha(opacity=50)";
      cursor: default; } }
  .xdsoft_time_box > div > div.xdsoft_disabled {
    opacity: .5;
    -ms-filter: "alpha(opacity=50)";
    cursor: default; }
  .xdsoft_calendar td {
    &.xdsoft_other_month.xdsoft_disabled {
      opacity: .2;
      -ms-filter: "alpha(opacity=20)"; }
    &:hover {
      color: #fff !important;
      background: #ff8000 !important;
      box-shadow: none !important; } }
  .xdsoft_timepicker .xdsoft_time_box > div > div:hover {
    color: #fff !important;
    background: #ff8000 !important;
    box-shadow: none !important; }
  .xdsoft_calendar td.xdsoft_current.xdsoft_disabled:hover, .xdsoft_timepicker .xdsoft_time_box > div > div.xdsoft_current.xdsoft_disabled:hover {
    background: #3af !important;
    box-shadow: #178fe5 0 1px 3px 0 inset !important;
    color: #fff !important; }
  .xdsoft_calendar td.xdsoft_disabled:hover, .xdsoft_timepicker .xdsoft_time_box > div > div.xdsoft_disabled:hover {
    color: inherit !important;
    background: inherit !important;
    box-shadow: inherit !important; }
  .xdsoft_calendar th {
    font-weight: 700;
    text-align: center;
    color: #999;
    cursor: default; }
  .xdsoft_copyright {
    color: #ccc !important;
    font-size: 10px;
    clear: both;
    float: none;
    margin-left: 8px;
    a {
      color: #eee !important;
      &:hover {
        color: #aaa !important; } } } }

.xdsoft_time_box {
  position: relative;
  border: 1px solid #ccc; }

.xdsoft_scrollbar {
  > .xdsoft_scroller {
    background: #ccc !important;
    height: 20px;
    border-radius: 3px; }
  position: absolute;
  width: 7px;
  right: 0;
  top: 0;
  bottom: 0;
  cursor: pointer; }

.xdsoft_datetimepicker.xdsoft_rtl .xdsoft_scrollbar {
  left: 0;
  right: auto; }

.xdsoft_scroller_box {
  position: relative; }

.xdsoft_datetimepicker.xdsoft_dark {
  box-shadow: 0 5px 15px -5px rgba(255, 255, 255, 0.506);
  background: #000;
  border-bottom: 1px solid #444;
  border-left: 1px solid #333;
  border-right: 1px solid #333;
  border-top: 1px solid #333;
  color: #ccc;
  .xdsoft_timepicker .xdsoft_time_box {
    border-bottom: 1px solid #222;
    > div > div {
      background: #0a0a0a;
      border-top: 1px solid #222;
      color: #999; } }
  .xdsoft_label {
    background-color: #000;
    > .xdsoft_select {
      border: 1px solid #333;
      background: #000;
      > div > .xdsoft_option {
        &:hover {
          color: #000;
          background: #007fff; }
        &.xdsoft_current {
          background: #c50;
          box-shadow: #b03e00 0 1px 3px 0 inset;
          color: #000; } } }
    i {
      background-image: url(data:image/png;base64,); } }
  .xdsoft_prev, .xdsoft_next, .xdsoft_today_button {
    background-image: url(data:image/png;base64,); }
  .xdsoft_calendar {
    td {
      background: #0a0a0a;
      border: 1px solid #222;
      color: #999; }
    th {
      background: #0a0a0a;
      border: 1px solid #222;
      color: #999;
      background: #0e0e0e; }
    td {
      &.xdsoft_today {
        color: #c50; }
      &.xdsoft_highlighted_default {
        background: #ffe9d2;
        box-shadow: #ffb871 0 1px 4px 0 inset;
        color: #000; }
      &.xdsoft_highlighted_mint {
        background: #c1ffc9;
        box-shadow: #00dd1c 0 1px 4px 0 inset;
        color: #000; }
      &.xdsoft_default, &.xdsoft_current {
        background: #c50;
        box-shadow: #b03e00 0 1px 3px 0 inset;
        color: #000; } } }
  .xdsoft_timepicker .xdsoft_time_box > div > div.xdsoft_current {
    background: #c50;
    box-shadow: #b03e00 0 1px 3px 0 inset;
    color: #000; }
  .xdsoft_calendar td:hover, .xdsoft_timepicker .xdsoft_time_box > div > div:hover {
    color: #000 !important;
    background: #007fff !important; }
  .xdsoft_calendar th {
    color: #666; }
  .xdsoft_copyright {
    color: #333 !important;
    a {
      color: #111 !important;
      &:hover {
        color: #555 !important; } } } }

.xdsoft_dark {
  .xdsoft_time_box {
    border: 1px solid #333; }
  .xdsoft_scrollbar > .xdsoft_scroller {
    background: #333 !important; } }

.xdsoft_datetimepicker {
  .xdsoft_save_selected {
    display: block;
    border: 1px solid #ddd !important;
    margin-top: 5px;
    width: 100%;
    color: #454551;
    font-size: 13px; }
  .blue-gradient-button {
    font-family: "museo-sans","Book Antiqua",sans-serif;
    font-size: 12px;
    font-weight: 300;
    color: #82878c;
    height: 28px;
    position: relative;
    padding: 4px 17px 4px 33px;
    border: 1px solid #d7d8da;
    background: -moz-linear-gradient(top, #fff 0, #f4f8fa 73%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0, #fff), color-stop(73%, #f4f8fa));
    background: -webkit-linear-gradient(top, #fff 0, #f4f8fa 73%);
    background: -o-linear-gradient(top, #fff 0, #f4f8fa 73%);
    background: -ms-linear-gradient(top, #fff 0, #f4f8fa 73%);
    background: linear-gradient(to bottom, #fff 0, #f4f8fa 73%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fff',endColorstr='#f4f8fa',GradientType=0);
    &:hover, &:focus, &:hover span, &:focus span {
      color: #454551;
      background: -moz-linear-gradient(top, #f4f8fa 0, #FFF 73%);
      background: -webkit-gradient(linear, left top, left bottom, color-stop(0, #f4f8fa), color-stop(73%, #FFF));
      background: -webkit-linear-gradient(top, #f4f8fa 0, #FFF 73%);
      background: -o-linear-gradient(top, #f4f8fa 0, #FFF 73%);
      background: -ms-linear-gradient(top, #f4f8fa 0, #FFF 73%);
      background: linear-gradient(to bottom, #f4f8fa 0, #FFF 73%);
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#f4f8fa',endColorstr='#FFF',GradientType=0); } } }
